<template>
  <div class="message-page">
    <header class="title-box">
      <div class="title">
        <div
          class="text"
          :class="{active:active == index}"
          v-for="(item, index) in tabList"
          :key="index"
          @click="change(index)"
        >{{ item }}</div>
      </div>
      <div class="write" @click="write">
        <img src="@/assets/image/write.png" class="write-icon" alt />写留言
      </div>
    </header>
    <template v-if="active === 0">
      <ul class="message-box" v-if="messageList.length">
        <li class="message" v-for="(item,index) in messageList" :key="index">
          <MessageItem :item="item" :index="index" @flower="flower"></MessageItem>
        </li>
      </ul>
      <div class="no-list" v-else>暂无留言</div>
    </template>
    <template v-else>
      <ul class="message-box" v-if="myList.length">
        <li class="message" v-for="(item,index) in myList" :key="index">
          <MessageItem :item="item" :index="index" @flower="flower"></MessageItem>
        </li>
      </ul>
      <div class="no-list" v-else>暂无留言</div>
    </template>
  </div>
</template>

<script>
import MessageItem from "@/components/message-item";

export default {
  components: {
    MessageItem
  },
  data() {
    return {
      messageList: [],
      myList: this.$store.state.myList,
      tabList: ["留言板", "我的"],
      active: 0
    };
  },
  mounted() {
    const list = [
      {
        username: "张国荣",
        userMessage:
          "张国荣张国荣张国荣张国荣张国荣张国荣张国荣张国荣张国荣张国荣",
        time: "1584684329",
        isFlower: 1,
        status: 2,
        number: 1314
      },
      {
        username: "Leslie",
        userMessage:
          "LeslieLeslieLeslieLeslieLeslieLeslieLeslieLeslieLeslieLeslie",
        time: "1584684329",
        isFlower: 0,
        status: 2,
        number: 520
      }
    ];
    for (let i = 0; i < 10; i++) {
      this.messageList.push(
        JSON.parse(JSON.stringify(list[0])),
        JSON.parse(JSON.stringify(list[1]))
      );
    }
  },
  methods: {
    change(index) {
      this.active = index;
    },
    flower(index) {
      if (this.messageList[index].isFlower) {
        this.messageList[index].isFlower = 0;
        this.messageList[index].number--;
      } else {
        this.messageList[index].isFlower = 1;
        this.messageList[index].number++;
      }
    },
    write() {
      this.$router.push({ name: "Write" });
    }
  },
  computed: {
    // myList() {
    //   if (!this.$store.state.myList.length) return [];
    //   return this.$store.state.myList.reverse();
    // }
  }
};
</script>

<style lang="scss">
.message-page {
  height: fill-available;
  background-color: #fff;
  & > .title-box {
    z-index: 2;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 1rem;
    padding: 0 0.3rem;
    border-bottom: 0.01rem solid #ededed;
    box-sizing: border-box;
    @include flex(space-between, center, row, nowrap);
    background-color: #fff;
    .title {
      display: flex;
      .text {
        margin-right: 0.3rem;
        line-height: 0.6rem;
        box-sizing: border-box;
        @include font-style(0.34rem, #666, 400);
      }
      .active {
        border-bottom: 0.04rem solid #eb7350;
        @include font-style(0.34rem, #333, 500);
      }
    }
    .write {
      width: 1.8rem;
      height: 0.6rem;
      line-height: 0.6rem;
      background-color: #eb7350;
      border-radius: 0.4rem;
      @include font-style(0.28rem, #fff, 400);
      @include flex(flex-start, center, row, nowrap);
      .write-icon {
        width: 0.4rem;
        height: 0.4rem;
        margin-left: 0.2rem;
        margin-right: 0.15rem;
      }
    }
  }

  .message-box {
    width: 100%;
    min-height: fill-available;
    box-sizing: border-box;
    padding: 1rem 0.3rem 0 0.3rem;
    .message {
      width: 100%;
      padding: 0.3rem 0;
      border-bottom: 0.01rem solid #ededed;
      box-sizing: border-box;
      @include flex(flex-start, flex-start, column, nowrap);
      &:last-child {
        border: none;
      }
    }
  }
  .no-list {
    height: fill-available;
    box-sizing: border-box;
    padding-top: 60%;
    background-color: #fff;
    text-align: center;
    @include font-style(0.28rem, #666, 400);
  }
}
</style>