<template>
  <div class="message-item-page">
    <div class="username ellipsis">{{ item.username }}</div>
    <div class="user-message">{{ item.userMessage }}</div>
    <div class="flower-box">
      <div class="time">{{ item.time | time }}</div>
      <template v-if="item.status === 2">
        <transition name="scale" mode="out-in">
          <img
            src="@/assets/image/flower-y.png"
            alt
            class="flower"
            v-if="item.isFlower"
            key="add"
            @click="flower()"
          />
          <img
            src="@/assets/image/flower-n.png"
            alt
            class="flower"
            v-else
            key="minus"
            @click="flower()"
          />
        </transition>
        <div class="number">{{ item.number }}</div>
      </template>
      <div class="check" :class="'check' + item.status">{{ check[item.status] }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["item", "index"],
  data() {
    return {
      check: ["正在审核中", "审核未通过"]
    };
  },
  methods: {
    flower() {
      this.$emit("flower", this.index);
    }
  },
  filters: {
    time(t) {
      const times = new Date(t * 1000);
      return `${times.getMonth() + 1}-${times.getDate()} ${
        times.getHours() < 10 ? "0" + times.getHours() : times.getHours()
      }:${
        times.getMinutes() < 10 ? "0" + times.getMinutes() : times.getMinutes()
      }`;
    }
  }
};
</script>

<style lang="scss">
.message-item-page {
  width: 100%;

  .username {
    width: 100%;
    line-height: 0.4rem;
    @include font-style(0.28rem, #666, 400);
  }
  .user-message {
    margin-top: 0.1rem;
    line-height: 0.4rem;
    white-space: wrap;
    word-break: break-all;
    @include font-style(0.28rem, #333, 400);
  }
  .flower-box {
    width: 100%;
    margin-top: 0.2rem;
    @include flex(flex-start, center, row, nowrap);
    .time {
      flex: 1;
      @include font-style(0.24rem, #999, 400);
    }
    .flower {
      width: 0.4rem;
      height: 0.4rem;
      margin-right: 0.1rem;
      background-size: 100% 100%;
      background-color: transparent;
    }
    .number {
      @include font-style(0.24rem, #808080, 400);
    }
    .check {
      @include font-style(0.24rem, #1989fa, 400);
    }
    .check1 {
      color: #df2020;
    }
  }
  .scale-enter-active {
    transition: all 1s ease;
  }
  .scale-leave-active {
    transition: all 0;
  }
  .scale-enter, .scale-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: scale(1.5);
    opacity: 0.5;
  }
}
</style>