<template>
  <div class="write-page">
    <div class="title-box">
      <div class="title">Dear Leslie:</div>
      <button class="sure" :disabled="!message" :class="{'can-sure': message}" @click="sure">确认</button>
    </div>
    <van-field
      class="message"
      v-model.trim="message"
      autosize
      type="textarea"
      placeholder="你好我是 Leslie，有事请留言..."
    />
  </div>
</template>

<script>
import { Field, Toast } from "vant";
export default {
  components: {
    [Field.name]: Field,
    [Toast.name]: Toast
  },
  data() {
    return {
      message: ""
    };
  },
  methods: {
    sure() {
      const obj = {
        username: this.username,
        userMessage: this.message,
        time: Date.parse(new Date()) / 1000,
        isFlower: 0,
        number: 0,
        status: 0
      };
      this.$store.commit("setMyList", obj);
      this.$toast("留言成功，正在审核");
      setTimeout(() => {
        this.$router.go(-1);
      }, 2000);
    }
  },
  computed: {
    username() {
      return this.$store.state.username;
    }
  }
};
</script>

<style lang="scss">
.write-page {
  width: 100%;
  @include flex(flex-start, flex-start, column, nowrap);
  .title-box {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 1rem;
    padding: 0 0.3rem;
    border-bottom: 0.01rem solid #ededed;
    box-sizing: border-box;
    @include flex(space-between, center, row, nowrap);
    background-color: #fff;
    .title {
      @include font-style(0.34rem, #333, 600);
    }
    .sure {
      height: 100%;
      padding: 0;
      background-color: transparent;
      border: none;
      @include font-style(0.32rem, #ffc09f, 400);
      @include flex(center, center, row, nowrap);
    }
    .can-sure {
      color: #eb7350;
    }
  }
  .message {
    margin-top: 1rem;
    @include font-style(0.34rem, #333, 400);
  }
}
</style>