<template>
  <div class="login-page">
    <img class="header" src="@/assets/image/header.jpg" alt />
    <div class="wrapper">
      <img class="input-icon" src="@/assets/image/user.png" alt />
      <input type="text" name="username" id="username" v-model.trim="username" placeholder="用户名" />
    </div>
    <div class="wrapper">
      <img class="input-icon" src="@/assets/image/password.png" alt />
      <input type="password" name="password" id="password" v-model.trim="password" placeholder="密码" />
    </div>
    <button
      class="login"
      :disabled="!username || !password"
      :class="{'can-login': username && password}"
      @click="login"
    >登录</button>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    login() {
      // 登陆
      this.$store.commit("setUsername", this.username);
      this.$router.push({ name: "Message" });
    }
  }
};
</script>

<style lang="scss">
.login-page {
  height: fill-available;
  @include flex(flex-start, center, column, nowrap);
  .header {
    width: 3rem;
    height: 3rem;
    margin-top: 2.5rem;
    margin-bottom: 1.5rem;
    border-radius: 100%;
    box-sizing: border-box;
    border: 0.01rem solid #ededed;
  }
  .wrapper {
    width: 4.8rem;
    height: 0.88rem;
    margin-bottom: 0.4rem;
    padding-bottom: 0.2rem;
    box-sizing: border-box;
    border-bottom: 0.01rem solid #ededed;
    @include flex(flex-start, center, row, nowrap);
    .input-icon {
      width: 0.5rem;
      height: 0.5rem;
      margin-right: 0.2rem;
    }
    input {
      flex: 1;
      height: 100%;
      line-height: 0.68rem;
      border: none;
      border-radius: 0.34rem;
      background: transparent;
      outline: none;
      @include font-style(0.33rem, #333, 400);
    }
    input[type="text"]:focus,
    input[type="password"]:focus {
      background: transparent;
      outline: none;
    }
  }

  .login {
    margin-top: 1rem;
    padding: 0;
    border: none;
    width: 4.8rem;
    height: 0.8rem;
    border-radius: 0.4rem;
    background-color: #f4f4f4;
    @include font-style(0.33rem, #333, 500);
    @include flex(center, center, row, nowrap);
  }
  .can-login {
    background-color: #07c160;
    color: #fff;
  }
}
</style>